.buttonContainer {
    text-align: end;
    margin-right: 25px;
}

.button {
    display: inline-block;
    margin: 5px;
    padding: 10px 15px;
    color: white;
    background: #a4c3b2;
    border: none;
    outline: none;
    border-radius: 5px;
    transition: .1s;
    text-decoration: none;
    margin-top: 25px;
}

.button:hover,
.button:focus {
    background: #82ac9a;
}