.ArticleManager {
    width: 65%;
    min-width: 180px;
    margin: 0 auto;
    padding: 10px 15px;
    background: #dff1e4;
    border-radius: 5px;
}

.button {
    margin: 5px;
    padding: 10px 15px;
    color: white;
    background: #a4c3b2;
    border: none;
    outline: none;
    border-radius: 5px;
    transition: .1s;
    cursor: pointer;
}

.button:hover,
.button:focus {
    background: #82ac9a;
}