.Article {
    padding: 30px;
}

.content {
    text-align: left;
    margin: 70px 0 35px 0 ;
    
}

.mainContent {
    line-height: 1.6em;
}

.summary {
    color: #00000080;
}

.buttons {
    margin: 15px 0;
    text-align: end;
    
    
}

.editLink {
    display: inline-block;
}

.buttons button {
    margin: 5px;
    padding: 10px 15px;
    color: white;
    background: #a4c3b2;
    border: none;
    outline: none;
    border-radius: 5px;
    transition: .1s;
    cursor: pointer;
}

.buttons button:hover,
.buttons button :focus {
    background: #82ac9a;
}

.articleFooter {
    background: #f1f1f1;
    border-radius: 5px;
    padding: 10px;
}

.author {
    margin-right: 10px;
}

.date {
    font-size: .9rem;
}