.Navigation {
    list-style-type: none;
    margin: 0;
    padding: 0;
    display: flex;
}

.button {
    margin: 5px;
    padding: 0 15px;
    color: white;
    background: #a4c3b2;
    border: none;
    outline: none;
    border-radius: 5px;
    transition: .1s;
    cursor: pointer;
}

.button:hover,
.button:focus {
    background: #82ac9a;
}