.Input {
    display: flex;
    flex-direction: column;
    align-items: start;
    margin-bottom: 20px;
    text-align: left;
}

.Input input::placeholder, .Input textarea::placeholder {
    font-family: Arial, Helvetica, sans-serif;
}

.Input label {
    text-transform: uppercase;
    font-weight: 600;
    color: #14746f;
}

.Input input, .Input textarea, .Input select {
    width: 100%;
    padding: 5px;
    font-size: 1rem;
    border: #14746f 1px solid;
    border-radius: 3px;
    font-family: Arial, Helvetica, sans-serif;
    margin-top: 3px;
}

.Input span {
    margin-left: 5px;
    color: #ff002b;
    font-size: 1rem;
}

.invalid {
    border: 1px solid #ff002b !important;
}